/* Banner Text content */
.banner-text-content {
	margin-top: 80px;
	color: $white-color;
  @include tablet {
		margin-top: 0;
  }
  h5 {
    font-size: 14px;
    letter-spacing: 5px;
    font-weight: 500;
    text-transform: uppercase;
    color: $white-color;
    line-height: 20px;
    @include tablet {
      font-size: 16px;
      letter-spacing: 10px;
      margin-bottom: 5px;
    }
		@include desktops {
			font-size: 18px;
		}
  }
  h1 {
    font-size: 40px;
    font-weight: 600;
    line-height: 40px;
    text-transform: uppercase;
    text-transform: uppercase;
		color: $white-color;
		@include tablet {
      font-size: 40px;
      line-height: 40px;
			font-weight: 700;
      margin-bottom: 15px;
    }
    @include desktops {
      font-size: 55px;
      line-height: 55px;
    }
		@include large-desktops {
      font-size: 70px;
			line-height: 70px;
    }

  }
  p {
		display: none;
    @include tablet {

			display: block;
      font-size: 16px;
			line-height: 25px;
			font-size: 14px;
			color: $white-color;
    }
  }
	.btn-primary {
		margin-top: 30px;
		width: 233px;
	  height: 77px;
		border: 1px solid $white-color;
		background: transparent;
		text-align: left;
		i {
			font-size: 20px;
		}
		&:hover {
		}
		@include tablet {
			margin-top: 50px;
		}
	}

}

/* Banner Image */
.banner-image {
	// display: none;
	@include tablet {

	}
  img {
    max-width: 100%;
  }
}

/*======== 6.2 PROME VIDEO SECTION ========*/
.home-promo-video {
	.promo-video {
		padding-top: 50px;
		padding-bottom: 100px;
		position: relative;

	}
	.text {
		display: block;
		text-align: center;
		color: $white-color;
		font-size: 26px;
		line-height: 26px;
		font-family: 'Poppins', sans-serif;
		font-weight: 500;
		padding-bottom: 20px;
	}
	a.video-link {
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		width: 98px;
		height: 98px;
		line-height: 98px;
		border-radius: 50%;
		text-align: center;
		background-color: $white-color;
		@include transition(transform 0.5s ease);
		i {
			position: relative;
			font-size: 70px;
			line-height: 98px;
			display: inline-block;
			@include linear-gradient(135deg, rgba(150,143,244,1) 25%,rgba(128,163,232,1) 75%,rgba(106,182,219,1) 100%);
			-webkit-background-clip: text;
			-webkit-text-fill-color: transparent;
		}
	}
}


/*======== 6.4 TEAM MEMBER ========*/
.team {
	.card {
		border: 0;
		.card-img {
			border-radius: 50%;
		}
		.card-body {
			a {
				display: block;
				text-align: center;
				padding: 10px;
				h5 {
					font-size: 20px;
				}
			}
			span {
				display: block;
				color: $text-color;
				text-align: center;
			}
		}
	}
}
