/*======== 6.6 CONTACT ========*/
.contact {
	.media {
		i {
			font-size: 16px;
			display: inline-block;
			width: 20px;
			margin-top: 5px;
			z-index: 1;
			@include linear-gradient(135deg, rgba(150,143,244,1) 25%,rgba(128,163,232,1) 75%,rgba(106,182,219,1) 100%);
			-webkit-background-clip: text;
			-webkit-text-fill-color: transparent;

		}
		.media-body {
			margin-left: 15px;
		}
	}
}
.social-link {
  margin-top: 30px;
	margin-bottom: 30px;
	@include desktops {
		margin-bottom: 0;
	}
}

.contact-form {
	input.form-control {
		height: 52px;
		border: 1px solid rgb(239, 239, 243);
		border-radius: 2px;
		padding: 20px;
		margin-bottom: 10px;
		&:active,
		&:focus {
			border: 1px solid rgb(245, 245, 247);
		  box-shadow: 0px 2px 7px 0px rgba(0, 0, 0, 0.05);
		}
	}
	textarea.form-control {
		border: 1px solid rgb(239, 239, 243);
		border-radius: 2px;
		padding: 20px;
		margin-bottom: 15px;
		min-height: 192px;
		&:active,
		&:focus {
			border: 1px solid rgb(245, 245, 247);
		  box-shadow: 0px 2px 7px 0px rgba(0, 0, 0, 0.05);
		}
	}
}
