/* ================================================
--> 4. UI KIT
================================================== */
/* ======== 4.1 ACCORDION ======== */
.card.card-accordion {
  border: 0;
  margin: 15px 0;
  .card-header {
    padding: 0px;
    border-radius: 0px;
    border: none;
    h6 {
      position: relative;
      a:after {
        top: 50%;
        @include transform(translateY(-50%))
      }
    }
    a {
      display: block;
      padding: 15px 27px 15px 20px;
      line-height: 24px;
      @include linear-gradient(135deg, rgba(150,143,244,1) 25%,rgba(128,163,232,1) 75%,rgba(106,182,219,1) 100%);
      color: $white-color;
      border: 1px solid $primary-color;

    }
    a[data-toggle=collapse]:after {
      font-family: FontAwesome;
      font-style: normal;
      speak: none;
      display: inline-block;
      content: '\f106';
      float: right;
      line-height: 1;
      width: 22px;
      font-size: 20px;
      text-align: center;
      position: absolute;
      right: 15px;
    }
    a.collapsed {
      line-height: 24px;
      color: $title-color;
      @include linear-gradient($white-color, $white-color);
      border: 1px solid #EBEBEB;
      font-weight: 500;
    }
    a[data-toggle=collapse].collapsed:after {
      font-family: FontAwesome;
      font-style: normal;
      speak: none;
      display: inline-block;
      content: '\f107';
    }
  }
  .card-block {
    padding: 30px 20px;
    font-size: 14px;
    line-height: 26px;
    font-weight: 500;
    // box-shadow: 0px 7px 12px 0px rgba(0, 0, 0, 0.1);
    border-radius: 0;
  }
}

/* ======== 4.2 BADGE ======== */
.badge {
    padding: 5px;
    font-weight: 400;
    font-size: 12px;
}
.badge-default {
    background-color: #323f41;
    color: $white-color!important;
}

/* ======== 4.3 BATTON ======== */
.btn {
	border: none;
	border-radius: 2px;
	height: 52px;
	padding: 0 25px;
	font-size: 11px;
	line-height: 52px;
	font-weight: 500;
	text-transform: uppercase;
	cursor: pointer;
	color: $white-color;
	@include transition(all 0.3s ease-in-out);

	@include desktops {
		font-size: 12px;
	}
	&.focus, &:focus, &.active, &:active, &:hover {
		box-shadow: none;
	}
	&.btn-rounded {
		border-radius: 40px;
	}
	i {
		font-size: inherit;
	}
}

.btn-primary {
	@include linear-gradient(135deg, rgba(150,143,244,1) 25%,rgba(128,163,232,1) 75%,rgba(106,182,219,1) 100%);
	border: 1px solid transparent;
  position: relative;
  z-index: 1;
  &:before {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    content: "";
    @include linear-gradient($white-color, $white-color);
    opacity: 0;
    transition: opacity 0.3s linear;
    z-index: -1;
  }
	&:hover, &:focus, &.active, &:active {
    box-shadow: none !important;
    color: $primary-color !important;
	}
  &:hover {
    &:before{
      opacity: 1;
    }
  }
  &.btn-cta {
		background-color: rgba($primary-color, .3) !important;;
		&:hover {
			background-color: darken($primary-color, 10%) !important;;
		}
	}
}
.btn-img {
  display: inline-block;
  margin-bottom: 10px;
  @include tablet {
    margin-top: 30px;
  }
  @include large-desktops {
    margin-right: 20px;
    margin-top: 50px;
  }
  img {
    width: 100%;
  }
}
.btn-icon {
  height: 77px;
  border: 1px solid rgba($white-color, .4);
  display: inline-flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  padding: 20px;
  border-radius: 5px;
  margin-top: 30px;
  .icon {
    padding-right: 20px;
    svg {
      font-size: 45px;
      color: $white-color;
    }
  }
  .text {
    padding-right: 20px;
    text-align: left;
    h6 {
      color: $white-color;
    }
    span {
      display: block;
      font-weight: 500;
      color: $white-color;
      font-size: 20px;
    }
  }
}

.brand-icon-svg {
  fill: $white-color;
  fill-rule: evenodd;
}


.btn-black {
	background-color: $title-color;
	border: 1px solid $title-color;

	&:hover, &:focus, &.active, &:active{
		background-color: darken($primary-color, 10%);
		border: 1px solid darken($primary-color, 10%);
		color: $white-color;
	}
}

.btn-white{
	background-color: $white-color;
	color: $title-color;
	&:hover {
		color: $primary-color;
	}
}

.scroll-btn {
  position: relative;
}


@-webkit-keyframes mymove {
    0%   {top: -20px;}
    100% {top: 6px;}
}

@keyframes mymove {
  0%   {top: -20px;}
  100% {top: 6px;}
}

.btn-scroll-down {
	position: absolute;
	top: -80px;
	left: 50%;
	transform: translateX(-50%);
	width: 20px;
	height: 50px;
	line-height: 60px;
	background-color: $white-color;
	display: block;
	border: 1px solid rgb(233, 233, 233);
	border-radius: 30px;
	z-index: 100;
	text-align: center;
  @include transition(all 0.3s ease-in);
	@include tablet {
		top: -110px;
		width: 28px;
		height: 68px;
		line-height: 90px;
	}
	i {
		color: rgb(160, 162, 166);
		display: inline-block;
		font-size: 16px;
    @include transition(all 0.3s ease-in);
    position: relative;


		@include tablet {
			font-size: 24px;
		}
	}
	&:hover{

		i{
      -webkit-animation: mymove 2s infinite;
      animation: mymove 2s infinite;

		}
	}
}

.btn-social {
	width: 45px;
	height: 45px;
	text-align: center;
	padding: 0;
	border-radius: 0;
	margin: 0 auto;
	line-height: 45px;
	display: inline-block;
  background-color: rgba(106, 200, 208, 0);
  border: 1px solid rgb(229, 229, 229);
  transition: all .5s ease-in-out;
  position: relative;
  z-index: 1;
  &:after {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    content: "";
    @include linear-gradient(135deg, rgba(150,143,244,1) 25%,rgba(128,163,232,1) 75%,rgba(106,182,219,1) 100%);
    opacity: 0;
    transition: opacity 0.5s linear;
    z-index: -1;
  }
	i {
		margin-right: 0px;
    color: $text-color;
    transition: all .5s ease-in-out;
    font-size: 18px;
	}
  &:hover {
    &:after{
      opacity: 1;
    }
    i {
      color: $white-color;
    }
  }
}

/* ======== 4.4 SECTION TITLE ======== */
[class*="heading-"] {
    position: relative;
}
h3.section-title{
	position: relative;
	font-size: 26px;
	line-height: 26px;
	font-weight: 500;
	padding-bottom: 15px;
	&:after {
		position: absolute;
		left: 0;
		bottom: 5px;
		// background-image: $primary-gradient;
		content: "";
		width: 100px;
		height: 1px;
	}
  &.text-white {
    &:after {
      @include linear-gradient($white-color, $white-color);
    }
  }
}

h3.section-title-center{
	position: relative;
	font-size: 26px;
	line-height: 26px;
	font-weight: 500;
	padding-bottom: 15px;
	&:after {
		position: absolute;
		left: 50%;
		bottom: 0;
		@include linear-gradient(135deg, rgba(150,143,244,1) 25%,rgba(128,163,232,1) 75%,rgba(106,182,219,1) 100%);
		content: "";
		width: 100px;
		height: 1px;
		transform: translateX(-50%);
	}
}
.section-padding {
	padding-top: 70px;
	padding-bottom: 70px;
	@include desktops {
		padding-top: 100px;
		padding-bottom: 100px;
	}
}

.section_top-padding {
	padding-top: 70px;
	@include desktops {
		padding-top: 90px;
	}
}
.section_bottom-padding {
	padding-bottom: 70px;
	@include desktops {
		padding-bottom: 90px;
	}
}

/* ======== 4.5 CARD ======== */
.card-images {
  background-color: rgb(249, 249, 249);
  border: 1px solid transparent;
  transition: all .5s;
  .card-header {
    background-size: contain;
    background-repeat: no-repeat;
    background-color: transparent;
    border-bottom: 0;
    position: relative;
    padding: 30px;
    &::after {
      position: absolute;
    }
    img {
      padding: 35px;
    }
    .badge {
      position: absolute;
      right: 30px;
      padding: 8px 15px;
      top: 30px;
      border-radius: 0;
			text-transform: uppercase;
			font-size: 12px;
			z-index: 10;
			&.badge-default{
				background-color: $title-color;
			}
    }
    .card-img-overlay {
      position: absolute;
      padding: 0;
      text-align: center;
      background-color: rgba(255, 255, 255, .5);
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      visibility: hidden;
      opacity: 0;
      transition: all .5s;
      transform: scale(0);
			width: 100%;
      .btn {
        width: 121px;
        padding: 0;
      }
    }
  }
  .card-body {
    padding: 15px 30px 30px;
    .content {
      position: relative;
      .price {
        font-size: 16px;
        color: $primary-color;
        font-weight: 600;
        line-height: 34px;
        @include desktops {
          font-size: 20px;
        }
        del {
          font-size: 14px;
          color: #999;
          font-weight: 400;
          margin-left: 10px;
          @include desktops {
            font-size: 16px;
          }
        }
      }
      .rating {
        i{
          color: $primary-color;
          line-height: 30px;
        }
      }
    }
  }
  &:hover {
    background-color: $white-color;
    box-shadow: 0px 10px 16px 0px rgba(0, 0, 0, 0.1);
    border: 1px solid $border-color;
    .card-header {
      .card-img-overlay {
        visibility: visible;
        opacity: 1;
        transform: scale(1);
      }
    }
    .card-body {
      .card-title{
        &:hover {
          color: $title-color !important;
        }
      }
    }
  }
}

/* ======== 4.6 TABS ======== */
.nav-tabs.feature-tabs {
	.nav-item {
		.nav-link {
			margin-right: 0;
			background-color: transparent;
			border: 0;
			padding: 0;
			margin: 0;
			&.active {
				border-top-color: transparent;
			}
		}
    &:hover {
			.media-style-01 {
				i{
					background-color: $primary-color;
					color: $white-color;
					font-size: 25px;
					text-align: center;
				}
			}
		}
	}
	border-bottom: 0;
}

/* Tab Image Frame */
.image-frame {
  position: relative;
  text-align: center;
  padding: 0px 20px;
  height: 604px;
  background-position: center center;
  background-repeat: no-repeat;
  background-size: 79%;

}

.tab-content.scren-image {
	position: absolute;
  left: 50%;
  top: 49.5%;
  transform: translate(-50%, -50%);
}


/* ======== 4.7 LIST STYLE ======== */
.list-style {
  @include tablet {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    height: 65px;
  }
	li {
		color: $title-color;
    line-height: 24px;
    padding: 3px 0;
		i {
			color: $title-color;
			margin-right: 10px;
			font-size: 18px;
      font-weight: 300;
		}
	}

}

/* List Style 2 */
.list-style2 {
	li {
    padding-bottom: 10px;
    color: $white-color;
    font-size: 16px;
    font-weight: 400;
    i {
      color: $white-color;
      margin-right: 10px;
      font-size: 18px;
    }
  }
}

/*  List Style 3 */
.list-style3 {
	li{
		color: #cbd0d8;
		margin-bottom: 20px;
		a{
			text-decoration: none;
			color: #cbd0d8;
			text-transform: uppercase;
			&:hover {
				color: $primary-color;
			}

		}
		i {
			margin-right: 20px;
			color: $primary-color;
		}
	}
}

/*======== 4.8 MEDIA STYLE ========*/
/* Media Style One */
.media-style-01 {
	transition: all .3s;
	padding: 15px 0;
  @include tablet {
    padding: 35px 0;
  }

  .media-body {
		h5 {
			font-size: 16px;
			font-weight: 500;
			margin-bottom: 15px;
		}
    p {
      font-weight: 400;
    }
	}
	&:last-child {
		.icon-style-1{
			&::after {
				display: none;
			}
		}
	}

}

/* Media Style Two */
.media-style-02 {
	.media-body {
		h5 {
			font-size: 16px;
			font-weight: 500;
		}
	}
	.icon-style-3 {
		&.custom-ml{
			@include tablet {
				margin-left: 20px;
			}
		}
		&.custom-mr{
			@include tablet {
				margin-right: 20px;
			}
		}
		i {
			color: #333;
		}
	}
}
.feature-style-one{
  flex-direction: column;
  .icon-style-1 {
    margin-top: 50px;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 25px;
  }
  .media-body {
    h5 {
      font-weight: 600;
      margin-bottom: 20px;
    }
  }
}
/*======== 4.9 ICON STYLE ========*/
.icon-style-1 {
	position: relative;
	width: 72px;
	height: 72px;
	line-height: 68px;
	text-align: center;
	border-radius: 50%;
	border: 2px solid $primary-color;
	transition: all .5s ease-in-out;
	margin-right: 20px;
	@include tablet {
		margin-right: 0
	}
	&.custom-mr {
		@include tablet {
			margin-right: 20px;
		}
	}
	&.custom-ml {
		@include tablet {
			margin-left: 20px;
		}
	}
	i {
		font-size: 25px;
		text-align: center;
		width: 62px;
    height: 62px;
    line-height: 62px;
		border-radius: 50%;
		margin-top: 3px;
		display: inline-block;
		transition: all .5s ease-in-out;
    @include linear-gradient(135deg, rgba(150,143,244,1) 25%,rgba(128,163,232,1) 75%,rgba(106,182,219,1) 100%);
   -webkit-background-clip: text;
   -webkit-text-fill-color: transparent;
   background-color: red;
	}
}

/*======== 4.10 IMAGE STYLE ========*/
.simple-img {
	text-align: center;
	img {
		width: 100%;
	}
}

.image-style-1 {
  @include tablet {
    position: absolute;
    right: 0;
    bottom: -115px;
    img {
      width: 100%;
    }
  }
  @include desktops {
    bottom: -170px;

  }
}

.image-style-2 {
  @include tablet {
    margin-top: 50px;
  }
  @include desktops {
    margin-right: 40px;
  }
  img {
    width: 100%;
    margin-top: -50px;
  }
}

.screen-shot-frame {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-image: url('../img/products/frame.png');
  z-index: 1000;
}
/*======== 4.11 CAROUSEL ========*/
/* Slick Arrow	*/
button.slick-prev {
	top: auto;
	left: 36%;
	bottom: -115px;
	width: 40px;
	height: 40px;
	border: 0;
	background-color: rgba(0, 0, 0, 0.4);
	transition: all .5s;
	@include tablet {
		left: 43%;
		bottom: -65%;
	}
  &:active,
  &:focus,
  &:visited,
	&:hover {
		background-color: darken($primary-color, 10%);
	}
}
button.slick-next {
	top: auto;
	right: 36%;
	bottom: -115px;
	width: 40px;
	height: 40px;
	border: 0;
	background-color: rgba(0, 0, 0, 0.4);
	transition: all .5s;
	@include tablet {
		right: 43%;
		bottom: -65%;
	}
  &:active,
  &:focus,
  &:visited,
	&:hover {
		background-color: darken($primary-color, 10%);
	}
}

.slick-prev:before {
	content: '\f104';
}
.slick-next:before {
	content: '\f105';
}
.slick-prev:before, .slick-next:before {
	font-family: 'FontAwesome';
	font-size: 20px;
	line-height: 1;
	opacity: 1;
	color: white;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

/* Slick Dots */
.slick-dots {
	bottom: 35px;
	li {
		margin: 0;
		button {
			border-radius: 50%;
			width: 10px;
			height: 10px;
			border: 1px solid $white-color;
			&:before {
				color: transparent;
			}
		}
		&.slick-active {
			button {
				background-color: $white-color;
				border: 1px solid $white-color;
				opacity: 1;
				&:before {
					color: transparent;
				}
			}
		}
	}
}

/*Banner */
.banner-row {
  display: table;
  height: 100vh;
}
.banner-image {
  img {
    max-width: 100%;
  }
}
/* Product */
.products {
  position: relative;
  &:after {
    position: absolute;
    top: 2px;
    left: 50%;
    transform: translateX(-50%);
    content:" ";
    background-image: url(../img/products/frame.png);
    width: 267px;
    height: 100%;
    height: 550px;
    background-repeat: no-repeat;
    background-size:  100%;
    z-index: 100;
    @include tablet {
      top: 25px;
      left: 62%;
      background-size: 55% 57%;
    }
    @include desktops {
      top: 12px;
      left: 54%;
      background-size: 80% 81.3%;
    }
    @include large-desktops {
      left: 50%;
      background-size: 99% 98.3%;
    }
  }
}

.product-slider {
  padding-top: 58px;
  padding-bottom: 110px;
  position: relative;

  .single-item {
    img {
      width: 100%;
      transform: scale(0.9);
      @include mobile {
        width: 65%;
        margin-left: 68px;
      }
      @include tablet {
        width: 100%;
        margin-left: 0;
      }
    }
  }
  .owl-dots {
    position: absolute;
    bottom: 0px;
    left: 50%;
    transform: translateX(-50%);
    // @include tablet {
    //   bottom: -30px;
    // }
    .owl-dot{
      span {
        background-color: transparent;
        border: 1px solid rgba(29, 32, 37, 0.2);
      }
      &.active {
        span {
          @include linear-gradient(135deg, rgba(150,143,244,1) 25%,rgba(128,163,232,1) 75%,rgba(106,182,219,1) 100%);
          border-color: transparent;
        }
      }
    }
  }
  .owl-item {
    &.active {
      &.center
      .single-item {
        img {
          transform: scale(1);
        }
      }
    }
  }

}


/*======== 4.12 PRICING TABLE ========*/

.pricing {
  padding: 50px 60px;
  text-align: center;
  border-color: rgb(229, 229, 229);
  border-radius: 3px;
  transition: all .3s;
  .card-header {
    background-color: transparent;
    padding-top: 0;
    padding-bottom: 30px;
    border-color: rgb(229, 229, 229);
  }
  .list-group {
    .list-group-item {
      border: 0;
      color: $title-color;
      span {
        font-size: 50px;
        line-height: 50px;
        font-family: 'Poppins', sans-serif;
        font-weight: 500;
        @include linear-gradient(135deg, rgba(150,143,244,1) 25%,rgba(128,163,232,1) 75%,rgba(106,182,219,1) 100%);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        padding-top: 15px;
        padding-bottom: 20px;
        display: block;
      }
    }
  }
  .btn-primary {
    background-image: none !important;
    background-color: transparent;
    border-width: 1px;
    border-style: solid;
    border-image: linear-gradient(#968ff4, #6ab6db) 1;
    color: $title-color;
    width: 162px;
    margin:  0 auto;
    margin-top: 40px;
    text-transform: uppercase;
    font-size: 14px;
    position: relative;
    z-index: 1;
    &:before {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      content: "";
      @include linear-gradient(135deg, rgba(150,143,244,1) 25%,rgba(128,163,232,1) 75%,rgba(106,182,219,1) 100%);
      opacity: 0;
      transition: opacity 0.5s linear;
      z-index: -1;
    }
    &:hover {
      &:before{
        opacity: 1;
      }
      color: $white-color !important;

    }
  }
  &:hover {
    box-shadow: 0px 7px 15px 0px rgba(0, 0, 0, 0.1);
    border: 1px solid transparent;
  }
}


/*======== 4.13 TESTIMONIAL ========*/
.testimonial-carousel {
  padding-bottom: 90px;
  .owl-nav {
    position: relative;
    .owl-prev ,
    .owl-next {
      display: inline-block;
      width: 40px;
      text-align: center;
      line-height: 40px;
      position: absolute;
      // transform: translateX(-50%);
      background-color: rgba(165, 165, 165, 0.4);
      color: #fff;
      font-size: 20px;
      bottom: -90px;
      &:before{
        position: absolute;
        content: "";
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        @include linear-gradient(135deg, rgba(150,143,244,1) 25%,rgba(128,163,232,1) 75%,rgba(106,182,219,1) 100%);
        z-index: -1;
        transition: opacity 0.5s linear;
        opacity: 0;
      }
      &:hover {
        &:before{
          opacity: 1;
        }
      }
    }
    .owl-prev {
      right: 50.5%;
      // left: 37%;
