/* ================================================
--> 5. HEADER
================================================== */

/*======== Header Styles ========*/
header.header {
  position: absolute;
  left: 0;
  width: 100%;
  background-color: transparent;
  &.elements{
    position: relative;
  }
}

nav.navbar {
  position: relative;
  z-index: 1000;
  &.navbar-sticky {
    position: fixed;
    top: 0;
    width: 100%;
    background: #fff;
    // border-bottom: 1px solid $border-color;
    @include linear-gradient(135deg, rgba(150,143,244,1) 25%,rgba(128,163,232,1) 75%,rgba(106,182,219,1) 100%);
    .navbar-brand {
      .svgLogo {
        height: 42px;
      }
    }
    .nav-item {
      a.nav-link {
        padding-left: 20px;
        @include tablet {
          padding: 20px 10px;
          &.active {
            &::after {
              bottom: 20px;
            }
          }
        }
        @include desktops {
          padding: 25px 20px;
        }
        @include large-desktops {
          padding: 25px;
        }
      }
    &:last-child {
      a.nav-link {
        margin-top: 8px;
      }
    }
    }
  }
  .navbar-toggler {
    border: 1px solid $white-color;
    padding: 0px;
    color: $white-color;
    i{
      font-size: 40px;
    }
    &:focus{
      outline: none;
    }
  }
  .navbar-toggler:focus {
    outline: none;
  }
  > .container {
    position: relative;
  }

}
.navbar {
  .container {
    .collapse,
    .collapsing{
      margin-left: -15px;
      margin-right: -15px;
      @include linear-gradient(135deg, rgba(150,143,244,1) 25%,rgba(128,163,232,1) 75%,rgba(106,182,219,1) 100%);
      padding-bottom: 15px;
      @include tablet {
        background-image: none;
      }
    }
  }
}

.navbar-nav.site_nav {
  .nav-item {
    a.nav-link {
      display: block;
      font-weight: 400;
      padding: 10px 15px;
      font-size: 12px;
      color: $white-color;
      text-transform: uppercase;
      position: relative;
      @include tablet {
        padding: 30px 10px;
        font-size: 10px;
      }
      @include desktops {
        padding: 30px 20px;
        font-size: 14px;
      }
      @include large-desktops {
        padding: 40px 25px;
      }
      &.active {
        @include tablet {
          &::after {
            position: absolute;
            width: 65%;
            bottom: 34px;
            left: 50%;
            transform: translateX(-50%);
            content: "";
            background-color: $white-color;
            height: 2px;
          }
        }
      }
    }
    &.active {
    }
    &:last-child{
      a.nav-link {
        border: 1px solid $white-color;
        padding: 0px 25px;
        margin-top: 15px;
        border-radius: 2px;
        margin-left: 15px;
        height: 47px;
        line-height: 47px;
        display: inline-block;
        &::after {
          display: none;
          height: 40px;
          line-height: 40;
          @include tablet {
            display: block;
          }
        }
        @include tablet {
          height: 40px;
          line-height: 40px;
          margin-top: 20px;
          margin-left: 15px;
        }

        @include desktops {
          margin-top: 26px;
          height: 47px;
          line-height: 47px;
        }
      }
    }
  }
}

/* Fixed navber when scroll up */
.static {
  .navbar-sticky {
    display: none;
    border-bottom: 1px solid $border-color;
  }
}
.up-scroll {
	.navbar-scrollUp.is-visible {
		-webkit-transform: translate3d(0,100%,0);
		-moz-transform: translate3d(0,100%,0);
		-ms-transform: translate3d(0,100%,0);
		-o-transform: translate3d(0,100%,0);
		transform: translate3d(0,100%,0);
        background: $white-color;
        width: 100%;
		.nav-item {
      a.nav-link {
			  color: $title-color;
		  }
      &.btn-primary {
        .nav-link {
          color: $white-color;
        }
      }
    }

		.path-2 {
			fill: $title-color;
		}
		.navbar-toggler .burger-menu {
			color: $title-color;
		}
	}
  .navbar-scrollUp.is-fixed {
    position: fixed;
    top: -55px;
    -webkit-transition: -webkit-transform .2s;
    -moz-transition: -moz-transform .2s;
    transition: transform .2s;
    border-bottom: 1px solid #fff;
    background-color: rgba(255,255,255,.9);
    border-bottom: 1px solid $border-color;
    @include tablet {
      top: -98px;
    }

    @include large-desktops {
      top: -88px;
    }
  }
}

/* Make header width same with as body when position is set to fixed */
body.boxed .is-visible, body.boxed .navbar-sticky {
    @include desktops {
        max-width: 970px;
    }
    @include large-desktops {
        max-width: 1260px;
    }
}

/* Header logo */
.svgLogo {
  .cls-1 {
    fill: $white-color;
  }

  .cls-1, .cls-2 {
    fill-rule: evenodd;
  }

  .cls-2 {
    fill: $white-color;
  }
}
